*{
  margin:0px;
  padding:0px;
}
.header-container{
  width:100%;
  height:50px;
  display: flex;
  align-items: center;
}
.header-container>h1{
  color:white;
  margin-left: 10px;
}
.sidebar{
  height:100%;
  width:0px;
  border:1px solid gainsboro;
  z-index: 1;
  visibility: hidden;
  transition: 0.5s all ease-in-out;
  position: absolute;
  background-color: white;
}
.sidebar>li{
  list-style-type: none;
  padding:10px;
  width:100%;
  height:auto;
  text-align: center;
}
.content-container{
  width:100%;
  height:100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.main-col{
  display: flex;
}
.inputBar{
  width: 95%;
  height:auto;
  text-align: right;
  font-size: 50px;
  border:0px;
}
.inputBar:focus{
  border: 1px solid white;
  outline-color: white;
}
.buttonsContainer{
  width:100%;
  height:auto;
  border:1px solid;
  display: grid;
  grid-template-columns: auto auto auto auto;
  border: 0px;
}
.button{
  height:70px;
  background-color: white;
  border:1px solid gainsboro;
  color:black;
}